// src/lib/persistent-store.ts
// Áµ±‰∏ÄÁöÑ Store ÊåÅ‰πÖÂåñËàáÈõ¢Á∑öÂêåÊ≠•Â∑•ÂÖ∑
// ÊîØÊè¥ÔºöÊú¨Âú∞ localStorage + IndexedDB + Supabase Èõ≤Á´ØÂêåÊ≠•

import { v4 as uuidv4 } from 'uuid';

type SetState<T> = (partial: T | Partial<T> | ((state: T) => T | Partial<T>)) => void;
type GetState<T> = () => T;

// ============= Èõ¢Á∑öÂêåÊ≠•ÂäüËÉΩ =============
let offlineStore: any = null;

// ÂãïÊÖãÂ∞éÂÖ•Èõ¢Á∑ö storeÔºàÈÅøÂÖçÂæ™Áí∞‰æùË≥¥Ôºâ
const getOfflineStore = async () => {
  if (typeof window === 'undefined') return null;
  if (!offlineStore) {
    try {
      // ‰ΩøÁî®ÂãïÊÖã import ÈÅøÂÖçÂêåÊ≠•ËºâÂÖ•ÂïèÈ°å
      const module = await import('@/lib/offline/sync-manager');
      offlineStore = module.useOfflineStore;
    } catch (e) {
      console.warn('Èõ¢Á∑öÂêåÊ≠•Ê®°ÁµÑËºâÂÖ•Â§±ÊïóÔºåÂ∞áÂÉÖ‰ΩøÁî®Êú¨Âú∞ÂÑ≤Â≠ò', e);
    }
  }
  return offlineStore;
};

// Âä†ÂÖ•ÂæÖÂêåÊ≠•ÈöäÂàóÔºà‰∏çÁ´ãÂç≥ÂêåÊ≠•ÔºåÁ≠âÂæÖÂÆöÊúüÂêåÊ≠•Ôºâ
const addPendingChange = async (operation: {
  type: 'CREATE' | 'UPDATE' | 'DELETE';
  table: string;
  data: any;
  localId?: string;
}) => {
  // üì¶ Êö´ÊôÇÂÅúÁî®‰ª•‰øÆÂæ©Á∑®Ë≠ØÂïèÈ°å
  console.log('üì¶ Êú¨Âú∞Ê®°ÂºèÔºöË∑≥ÈÅéÂêåÊ≠•ÈöäÂàó');
  return;

  if (typeof window === 'undefined') return;

  const OfflineStore = await getOfflineStore();
  if (!OfflineStore) return;

  try {
    const store = OfflineStore.getState();

    // Ê™¢Êü•ÊòØÂê¶Â∑≤Â≠òÂú®Áõ∏ÂêåÁöÑÊìç‰ΩúÔºàÈÅøÂÖçÈáçË§áÔºâ
    const existingOp = store.pendingChanges.find((op: any) =>
      op.table === operation.table &&
      (op.data?.id === operation.data?.id || op.localId === operation.localId)
    );

    if (existingOp) {
      console.log(`Ë∑≥ÈÅéÈáçË§áÊìç‰Ωú: ${operation.table} ${operation.data?.id || operation.localId}`);
      return;
    }

    store.addPendingChange({
      id: uuidv4(),
      ...operation,
      timestamp: Date.now(),
      retryCount: 0,
      userId: 'current-user' // TODO: Âæû auth store ÂèñÂæó
    });
  } catch (error) {
    console.warn('Âä†ÂÖ•ÂêåÊ≠•ÈöäÂàóÂ§±Êïó:', error);
  }
};

// Ê™¢Êü•ÊòØÂê¶Âú®Á∑ö
const isOnline = (): boolean => {
  if (typeof window === 'undefined') return true;
  return navigator.onLine;
};

// ============= CRUD ÊñπÊ≥ïÁîüÊàêÂô®ÔºàÊîØÊè¥Èõ¢Á∑öÔºâ =============

/**
 * Âª∫Á´ãÂÖ∑ÊúâÈõ¢Á∑öÂêåÊ≠•ÂäüËÉΩÁöÑ CRUD ÊñπÊ≥ï
 *
 * @param tableName - Supabase Ë°®Ê†ºÂêçÁ®±
 * @param arrayKey - Store ‰∏≠Èô£ÂàóÁöÑ key ÂêçÁ®±ÔºàÂ¶Ç 'todos', 'tours'Ôºâ
 * @returns CRUD ÊñπÊ≥ïÁâ©‰ª∂
 *
 * @example
 * const todoMethods = createPersistentCrudMethods<Todo>('todos', 'todos');
 * // ÊúÉÁî¢Áîü: addTodo, updateTodo, deleteTodo, loadTodos
 */
export const createPersistentCrudMethods = <T extends { id?: string }>(
  tableName: string,
  arrayKey: string,
  set: SetState<any>,
  get: GetState<any>
) => {
  const capitalizedName = arrayKey.charAt(0).toUpperCase() + arrayKey.slice(1, -1);

  return {
    // ===== Êñ∞Â¢û =====
    [`add${capitalizedName}`]: async (data: Omit<T, 'id' | 'createdAt' | 'updatedAt'>) => {
      try {
        const id = uuidv4();
        const now = new Date().toISOString();

        let newItem = {
          ...data,
          id,
          createdAt: now,
          updatedAt: now
        } as T;

        // Ëá™ÂãïÁîüÊàêÁ∑®ËôüÔºàÂ¶ÇÊûúÊòØ quotes Ë°®Ê†º‰∏îÊ≤íÊúâ quoteNumberÔºâ
        if (tableName === 'quotes' && !(data as any).quoteNumber) {
          const year = new Date().getFullYear();
          const state = get();
          const existingQuotes = state[arrayKey] || [];
          const lastNumber = existingQuotes
            .map((q: any) => q.quoteNumber)
            .filter((num: string) => num?.startsWith(`QUOTE-${year}`))
            .map((num: string) => parseInt(num.split('-')[2] || '0'))
            .sort((a: number, b: number) => b - a)[0] || 0;

          const newNumber = (lastNumber + 1).toString().padStart(4, '0');
          (newItem as any).quoteNumber = `QUOTE-${year}-${newNumber}`;
        }

        // 1. Á´ãÂç≥Êõ¥Êñ∞Êú¨Âú∞ store
        const state = get();
        set({
          [arrayKey]: [...state[arrayKey], newItem]
        });

        // 2. Â≠òÂà∞ IndexedDB
        const { getSyncManager } = await import('@/lib/offline/sync-manager');
        const syncManager = getSyncManager();
        const localDb = syncManager.getLocalDb();
        if (localDb) {
          await localDb.put(tableName, newItem);
        }

        // 3. Á´ãÂç≥ÂêåÊ≠•Âà∞ SupabaseÔºàÂ¶ÇÊûúÂú®Á∑öÔºâ
        // ‚ö†Ô∏è Êö´ÊôÇÂÅúÁî® Supabase ÂêåÊ≠• - Á≠âÁ∂≤Á´ôÁ©©ÂÆöÂæåÂÜçÂïüÁî®
        /*
        if (isOnline()) {
          try {
            const { VenturoAPI } = await import('@/lib/supabase/api');
            await VenturoAPI.create(tableName, newItem);
            console.log(`‚úÖ ÊàêÂäüÂêåÊ≠•Êñ∞Â¢û ${tableName}:`, id);
          } catch (syncError) {
            console.error(`‚ùå ÂêåÊ≠•Êñ∞Â¢ûÂ§±Êïó ${tableName}:`, syncError);
            // ÂêåÊ≠•Â§±ÊïóÊôÇÂä†ÂÖ•ÈöäÂàó
            addPendingChange({
              type: 'CREATE',
              table: tableName,
              data: newItem,
              localId: id
            });
          }
        }
        */
        console.log(`üì¶ Êú¨Âú∞Ê®°ÂºèÔºöÊñ∞Â¢û ${tableName}:`, id);

        return newItem;
      } catch (error) {
        console.error(`‚ùå Êñ∞Â¢û ${tableName} Â§±Êïó:`, error);
        throw new Error(`ÁÑ°Ê≥ïÊñ∞Â¢û${tableName}: ${error instanceof Error ? error.message : 'Êú™Áü•ÈåØË™§'}`);
      }
    },

    // ===== Êõ¥Êñ∞ =====
    [`update${capitalizedName}`]: async (id: string, data: Partial<T>) => {
      try {
        const now = new Date().toISOString();
        const updateData = { ...data, updatedAt: now };

        // 1. Á´ãÂç≥Êõ¥Êñ∞Êú¨Âú∞ store
        const state = get();
        const updatedArray = state[arrayKey].map((item: T) =>
          item.id === id ? { ...item, ...updateData } : item
        );

        set({ [arrayKey]: updatedArray });

        const updatedItem = updatedArray.find((item: T) => item.id === id);

        // 2. Â≠òÂà∞ IndexedDB
        if (updatedItem) {
          const { getSyncManager } = await import('@/lib/offline/sync-manager');
          const syncManager = getSyncManager();
          const localDb = syncManager.getLocalDb();
          if (localDb) {
            await localDb.put(tableName, updatedItem);
          }
        }

        // 3. Á´ãÂç≥ÂêåÊ≠•Âà∞ SupabaseÔºàÂ¶ÇÊûúÂú®Á∑öÔºâ
        // ‚ö†Ô∏è Êö´ÊôÇÂÅúÁî® Supabase ÂêåÊ≠• - Á≠âÁ∂≤Á´ôÁ©©ÂÆöÂæåÂÜçÂïüÁî®
        /*
        if (isOnline()) {
          try {
            const { VenturoAPI } = await import('@/lib/supabase/api');
            await VenturoAPI.update(tableName, id, updateData);
            console.log(`‚úÖ ÊàêÂäüÂêåÊ≠•Êõ¥Êñ∞ ${tableName}:`, id);
          } catch (syncError) {
            console.error(`‚ùå ÂêåÊ≠•Êõ¥Êñ∞Â§±Êïó ${tableName}:`, syncError);
            // ÂêåÊ≠•Â§±ÊïóÊôÇÂä†ÂÖ•ÈöäÂàó
            addPendingChange({
              type: 'UPDATE',
              table: tableName,
              data: { id, ...updateData }
            });
          }
        }
        */
        console.log(`üì¶ Êú¨Âú∞Ê®°ÂºèÔºöÊõ¥Êñ∞ ${tableName}:`, id);

        return updatedItem;
      } catch (error) {
        console.error(`‚ùå Êõ¥Êñ∞ ${tableName} Â§±Êïó:`, error);
        throw new Error(`ÁÑ°Ê≥ïÊõ¥Êñ∞${tableName}: ${error instanceof Error ? error.message : 'Êú™Áü•ÈåØË™§'}`);
      }
    },

    // ===== Âà™Èô§ =====
    [`delete${capitalizedName}`]: async (id: string) => {
      try {
        // 1. Á´ãÂç≥Êõ¥Êñ∞Êú¨Âú∞ store
        const state = get();
        set({
          [arrayKey]: state[arrayKey].filter((item: T) => item.id !== id)
        });

        // 2. Âæû IndexedDB Âà™Èô§
        const { getSyncManager } = await import('@/lib/offline/sync-manager');
        const syncManager = getSyncManager();
        const localDb = syncManager.getLocalDb();
        if (localDb) {
          await localDb.delete(tableName, id);
        }

        // 3. Á´ãÂç≥ÂêåÊ≠•Âà∞ SupabaseÔºàÂ¶ÇÊûúÂú®Á∑öÔºâ
        // ‚ö†Ô∏è Êö´ÊôÇÂÅúÁî® Supabase ÂêåÊ≠• - Á≠âÁ∂≤Á´ôÁ©©ÂÆöÂæåÂÜçÂïüÁî®
        /*
        if (isOnline()) {
          try {
            const { VenturoAPI } = await import('@/lib/supabase/api');
            await VenturoAPI.delete(tableName, id);
            console.log(`‚úÖ ÊàêÂäüÂêåÊ≠•Âà™Èô§ ${tableName}:`, id);
          } catch (syncError) {
            console.error(`‚ùå ÂêåÊ≠•Âà™Èô§Â§±Êïó ${tableName}:`, syncError);
            // ÂêåÊ≠•Â§±ÊïóÊôÇÂä†ÂÖ•ÈöäÂàó
            addPendingChange({
              type: 'DELETE',
              table: tableName,
              data: { id }
            });
          }
        }
        */
        console.log(`üì¶ Êú¨Âú∞Ê®°ÂºèÔºöÂà™Èô§ ${tableName}:`, id);

        return true;
      } catch (error) {
        console.error(`‚ùå Âà™Èô§ ${tableName} Â§±Êïó:`, error);
        throw new Error(`ÁÑ°Ê≥ïÂà™Èô§${tableName}: ${error instanceof Error ? error.message : 'Êú™Áü•ÈåØË™§'}`);
      }
    },

    // ===== ËºâÂÖ•ÔºàÂêà‰ΩµÊú¨Âú∞ÂíåÈõ≤Á´ØË≥áÊñôÔºâ =====
    [`load${capitalizedName}s`]: async () => {
      try {
        const { getSyncManager } = await import('@/lib/offline/sync-manager');
        const syncManager = getSyncManager();
        const localDb = syncManager.getLocalDb();

        // 1. ÂÖàÂæû IndexedDB ËºâÂÖ•Êú¨Âú∞Ë≥áÊñô
        let localData: T[] = [];
        if (localDb) {
          const allLocalData = await localDb.getAll(tableName);
          localData = (allLocalData || []).filter((item: any) => !item.deleted);
        }

        // 2. Â¶ÇÊûúÂú®Á∑öÔºåÂæû Supabase ËºâÂÖ•Èõ≤Á´ØË≥áÊñô
        // ‚ö†Ô∏è Êö´ÊôÇÂÅúÁî® Supabase ÂêåÊ≠• - Á≠âÁ∂≤Á´ôÁ©©ÂÆöÂæåÂÜçÂïüÁî®
        /*
        if (isOnline()) {
          try {
            const { VenturoAPI } = await import('@/lib/supabase/api');
            const remoteData = await VenturoAPI.read<T>(tableName);

            if (remoteData) {
              // Âêà‰ΩµÊú¨Âú∞ÂíåÈõ≤Á´ØË≥áÊñôÔºà‰ΩøÁî® Map ÂéªÈáçÔºåÂÑ™ÂÖà‰ΩøÁî®Èõ≤Á´ØË≥áÊñôÔºâ
              const mergedMap = new Map<string, T>();

              // ÂÖàÂä†ÂÖ•Êú¨Âú∞Ë≥áÊñô
              localData.forEach(item => {
                if (item.id) mergedMap.set(item.id, item);
              });

              // Èõ≤Á´ØË≥áÊñôË¶ÜËìãÊú¨Âú∞ÔºàÈõ≤Á´ØÁÇ∫Ê∫ñÔºâ
              remoteData.forEach(item => {
                if (item.id) mergedMap.set(item.id, item);
              });

              const mergedData = Array.from(mergedMap.values());

              // Êõ¥Êñ∞ Store
              set({ [arrayKey]: mergedData });

              // Êõ¥Êñ∞ IndexedDBÔºà‰øùÊåÅÊú¨Âú∞ÂíåÈõ≤Á´Ø‰∏ÄËá¥Ôºâ
              if (localDb) {
                for (const item of mergedData) {
                  await localDb.put(tableName, { ...item, sync_status: 'synced' });
                }
              }

              console.log(`‚úÖ Âêà‰ΩµËºâÂÖ• ${tableName}: Êú¨Âú∞ ${localData.length} Á≠Ü + Èõ≤Á´Ø ${remoteData.length} Á≠Ü = ${mergedData.length} Á≠Ü`);
              return mergedData;
            }
          } catch (error) {
            console.warn(`‚ö†Ô∏è ÂæûÈõ≤Á´ØËºâÂÖ• ${tableName} Â§±ÊïóÔºå‰ΩøÁî®Êú¨Âú∞Ë≥áÊñô:`, error);
            // Â¶ÇÊûúÈõ≤Á´ØËºâÂÖ•Â§±ÊïóÔºå‰ΩøÁî®Êú¨Âú∞Ë≥áÊñô
            set({ [arrayKey]: localData });
            return localData;
          }
        } else {
          // Èõ¢Á∑öÊôÇÂè™‰ΩøÁî®Êú¨Âú∞Ë≥áÊñô
          set({ [arrayKey]: localData });
          console.log(`üì¶ Èõ¢Á∑öÊ®°ÂºèÔºåÂæûÊú¨Âú∞ËºâÂÖ• ${tableName}:`, localData.length, 'Á≠Ü');
          return localData;
        }
        */

        // Êú¨Âú∞Ê®°ÂºèÔºöÂè™‰ΩøÁî® IndexedDB Ë≥áÊñô
        set({ [arrayKey]: localData });
        console.log(`üì¶ Êú¨Âú∞Ê®°ÂºèÔºöËºâÂÖ• ${tableName}:`, localData.length, 'Á≠Ü');
        return localData;
      } catch (error) {
        console.error(`‚ö†Ô∏è ËºâÂÖ• ${tableName} Â§±Êïó:`, error);
        return null;
      }
    }
  };
};

// ============= ËºîÂä©ÂáΩÊï∏ =============

/**
 * ÁîüÊàêÂîØ‰∏Ä ID
 * ‰ΩøÁî® UUID v4 Á¢∫‰øùÂÖ®ÂüüÂîØ‰∏ÄÊÄß
 */
export const generateId = (): string => {
  return uuidv4();
};

/**
 * ÁîüÊàêÊôÇÈñìÊà≥
 */
export const generateTimestamp = (): string => {
  return new Date().toISOString();
};

/**
 * ÈåØË™§ËôïÁêÜÂåÖË£ùÂô®
 * Áµ±‰∏ÄËôïÁêÜÊâÄÊúâ store Êìç‰ΩúÁöÑÈåØË™§
 */
export const withErrorHandling = <T>(
  operation: () => T,
  errorMessage: string
): T => {
  try {
    return operation();
  } catch (error) {
    console.error(`‚ùå ${errorMessage}:`, error);
    throw new Error(`${errorMessage}: ${error instanceof Error ? error.message : 'Êú™Áü•ÈåØË™§'}`);
  }
};
